import { CHANGE_EVENT, PREFIX } from '../../utils/constants';
import { defineComponent, ref, watch, provide, openBlock, createElementBlock, normalizeClass, normalizeStyle, renderSlot } from 'vue';
import { usePrefix } from '../../utils/util';

var script = defineComponent({
  name: "Steps",
  props: {
    space: {
      type: [Number, String],
      default: ""
    },
    size: {
      type: [Number, String],
      default: ""
    },
    gap: {
      type: [Number, String],
      default: ""
    },
    active: {
      type: Number,
      default: 0
    },
    dir: {
      type: String,
      default: "horizontal",
      validator: (val) => ["horizontal", "vertical"].includes(val)
    },
    round: {
      type: Boolean,
      default: false
    },
    center: {
      type: Boolean,
      default: false
    },
    simple: {
      type: Boolean,
      default: false
    },
    finishStatus: {
      type: String,
      default: "finish",
      validator: (val) => ["wait", "process", "finish", "error", "success"].includes(val)
    },
    processStatus: {
      type: String,
      default: "process",
      validator: (val) => ["wait", "process", "finish", "error", "success"].includes(val)
    }
  },
  emits: [CHANGE_EVENT],
  setup(props, { emit }) {
    const { prefix } = usePrefix();
    const steps = ref([]);
    watch(steps, () => {
      steps.value.forEach((instance, index) => {
        instance.setIndex(index);
      });
    });
    provide("Steps", { props, steps });
    watch(() => props.active, (newVal, oldVal) => {
      emit(CHANGE_EVENT, newVal, oldVal);
    });
    return {
      prefix,
      steps
    };
  }
});

function render(_ctx, _cache, $props, $setup, $data, $options) {
  return openBlock(), createElementBlock("div", {
    class: normalizeClass([
      _ctx.prefix + "-steps",
      _ctx.dir
    ]),
    style: normalizeStyle([
      _ctx.size ? `--step-icon-wrap-size: ${_ctx.size}` : "",
      _ctx.gap ? `--step-gap: ${_ctx.gap}` : ""
    ])
  }, [
    renderSlot(_ctx.$slots, "default")
  ], 6);
}

script.render = render;
script.__file = "packages/components/steps/src/index.vue";

script.install = (app) => {
  app.component(PREFIX + script.name, script);
};
const _Steps = script;
const HlSteps = _Steps;

export default _Steps;
export { HlSteps };
