function HlError(m) {
    this.message = m;
    this.name = 'HlError';
}
HlError.prototype.toString = function () {
    return `[${this.name}]: ${this.message}`;
};
export default (scope, m) => {
    throw new HlError(`[${scope}] ${m}`);
};
export function warn(scope, m) {
    console.warn(new HlError(`[${scope}] ${m}`));
}
