import { isNumber } from './util';
export const isValidWidthUnit = (val) => {
    if (isNumber(val)) {
        return true;
    }
    else {
        return ['px', 'rem', 'em', 'vw', '%', 'vmin', 'vmax', 'auto'].some(unit => val.endsWith(unit));
    }
};
export const isValidComponentSize = (val) => {
    return ['xxxs', 'xxs', 'xs', 'sm', 'md', 'lg', 'xl', 'xxl', 'xxxl', null, undefined, ''].includes(val);
};
export const isValidComponentType = (val) => {
    return ['link', 'primary', 'success', 'danger', 'warning', 'info', null, undefined, ''].includes(val);
};
export const isValidComponentState = (val) => ['success', 'loading', 'fail'].includes(val);
export const isValidDatePickType = (val) => [
    'year',
    'month',
    'date',
    'dates',
    'week',
    'datetime',
    'datetimerange',
    'daterange',
    'monthrange',
].includes(val);
