import { PREFIX } from '../../utils/constants';
import { defineComponent, openBlock, createElementBlock, normalizeClass, renderSlot } from 'vue';
import { usePrefix } from '../../utils/util';

var script = defineComponent({
  name: "Main",
  setup() {
    const { prefix } = usePrefix();
    return {
      prefix
    };
  }
});

function render(_ctx, _cache, $props, $setup, $data, $options) {
  return openBlock(), createElementBlock("main", {
    class: normalizeClass([_ctx.prefix + "-main"])
  }, [
    renderSlot(_ctx.$slots, "default")
  ], 2);
}

script.render = render;
script.__file = "packages/components/layout/src/main.vue";

script.install = (app) => {
  app.component(PREFIX + script.name, script);
};
const _Main = script;
const HlMain = _Main;

export default _Main;
export { HlMain };
