import { watch, isRef, onMounted, onUnmounted } from 'vue';
import getScrollBarWidth from '../../utils/scrollbar-width';
import throwError from '../../utils/error';
import { animationEndName } from '../../utils/animation';
import { addClass, removeClass, hasClass, getStyle, } from '../../utils/dom';
/**
 * Hook that monitoring the ref value to lock or unlock the screen.
 * When the trigger became true, it assumes modal is now opened and vice versa.
 * @param trigger {Ref<boolean>}
 */
export default (trigger, args = {}) => {
    if (!isRef(trigger)) {
        throwError('[useLockScreen]', 'You need to pass a ref param to this function');
    }
    let scrollBarWidth = 0;
    let withoutHiddenClass = false;
    let bodyPaddingRight = '0';
    let computedBodyPaddingRight = 0;
    onMounted(() => {
        handleAnimationEndEvent('addEventListener');
    });
    onUnmounted(() => {
        handleAnimationEndEvent('removeEventListener');
        cleanup();
    });
    const handleAnimationEndEvent = (funcName) => {
        if (args.animationDom && args.animationDom.value) {
            const el = '$el' in args.animationDom.value ? args.animationDom.value.$el : args.animationDom.value;
            el[funcName](animationEndName, animationEndHandler);
        }
    };
    const animationEndHandler = (e) => {
        if (!trigger.value && (!args.subElementSelector || e.target.className.indexOf(args.subElementSelector) !== -1)) {
            cleanup();
        }
    };
    const cleanup = () => {
        const scrollCount = +document.body.getAttribute('scroll-count') - 1;
        if (isNaN(scrollCount) || scrollCount <= 0) {
            removeClass(document.body, 'no-scroll');
            if (withoutHiddenClass) {
                document.body.style.paddingRight = bodyPaddingRight;
            }
        }
        document.body.setAttribute('scroll-count', String(isNaN(scrollCount) || scrollCount <= 0 ? 0 : scrollCount));
    };
    watch(trigger, val => {
        if (val) {
            withoutHiddenClass = !hasClass(document.body, 'no-scroll');
            if (withoutHiddenClass) {
                bodyPaddingRight = document.body.style.paddingRight;
                computedBodyPaddingRight = parseInt(getStyle(document.body, 'paddingRight'), 10);
            }
            scrollBarWidth = getScrollBarWidth();
            const bodyHasOverflow = document.documentElement.clientHeight < document.body.scrollHeight;
            const bodyOverflowY = getStyle(document.body, 'overflowY');
            if (scrollBarWidth > 0 &&
                (bodyHasOverflow || bodyOverflowY === 'scroll') &&
                withoutHiddenClass) {
                document.body.style.paddingRight =
                    computedBodyPaddingRight + scrollBarWidth + 'px';
            }
            const scrollCount = +document.body.getAttribute('scroll-count'); // fix nested overlay bug
            if (isNaN(scrollCount) || scrollCount === 0) {
                addClass(document.body, 'no-scroll');
            }
            document.body.setAttribute('scroll-count', String(isNaN(scrollCount) ? 1 : scrollCount + 1));
        }
        else {
            if (!args.animationDom) {
                cleanup();
            }
        }
    });
};
